
import axios from 'axios'
import { defineComponent, PropType, ref, watch } from 'vue'
type UploadStatus='ready'|'loading'|'success'|'error'
type CheckFunction = (file: File) => boolean
export default defineComponent({
  name: 'Uploader',
  props: {
    action: {
      type: String,
      required: true
    },
    beforeUpload: {
      type: Function as PropType<CheckFunction>
    },
    uploade: {
      type: Object
    }
  },
  inheritAttrs: false,
  emits: ['file-uploaded', 'file-uploaded-error'],
  setup (props, context) {
    const fileInput = ref<null | HTMLInputElement>(null)
    const fileSatus = ref<UploadStatus>(props.uploade ? 'success' : 'ready')
    const upLoadedata = ref(props.uploade)
    watch(() => props.uploade, (newValue) => {
      if (newValue) {
        fileSatus.value = 'success'
        upLoadedata.value = newValue
      }
    })
    const triggerUpload = () => {
      if (fileInput.value) {
        fileInput.value.click()
      }
    }
    const handleFileChange = (e: Event) => {
      const currentTarget = e.target as HTMLInputElement
      if (currentTarget.files) {
        const files = Array.from(currentTarget.files)
        if (props.beforeUpload) {
          const result = props.beforeUpload(files[0])
          if (!result) {
            return
          }
        }
        fileSatus.value = 'loading'
        const formdata = new FormData()
        formdata.append('file', files[0])
        axios.post(props.action, formdata, {
          headers: {
            'Content-Type': 'mulipart/form-data'
          }
        }).then((resp: any) => {
          fileSatus.value = 'success'
          upLoadedata.value = resp.data
          context.emit('file-uploaded', resp.data)
        }).catch((error) => {
          fileSatus.value = 'error'
          context.emit('file-uploaded-error', error)
        }).finally(() => {
          if (fileInput.value) {
            fileInput.value.value = ''
          }
        })
      }
    }
    return { fileInput, triggerUpload, handleFileChange, fileSatus, upLoadedata }
  }
})
