
import PostList from '@/components/PostList.vue'
import { computed, defineComponent, onMounted } from 'vue'
import { useRoute } from 'vue-router'
import { useStore } from 'vuex'
import store, { GlobalDataProps, ColumnProps } from '@/store'
import { generateFitUrl } from '@/helper'
export default defineComponent({
  name: 'ColimnDetail',
  components: {
    PostList
  },
  setup () {
    const store = useStore<GlobalDataProps>()
    const route = useRoute()
    const currentId = route.params.id
    onMounted(() => {
      store.dispatch('fetchColumn', currentId)
      store.dispatch('fetchPosts', currentId)
    })
    const column = computed(() => {
      const selecrColumn = store.getters.getColumnById(currentId) as ColumnProps
      if (selecrColumn) {
        generateFitUrl(selecrColumn, 100, 100)
        if (!selecrColumn.avatar) {
          selecrColumn.avatar = {
            url: require('@/assets/logo.png')
          }
        }
      }
      return selecrColumn
    })
    const list = computed(() => store.getters.getPostsByid(currentId))
    return {
      route,
      column,
      list
    }
  }
})
