
// eslint-disable-next-line import/no-duplicates
import store from '@/store'
// eslint-disable-next-line import/no-duplicates
import { PostProps, GlobalDataProps, ResponseType, ImageProps } from '@/store'
import ValidateForm from '@/components/ValidateForm.vue'
// eslint-disable-next-line import/no-duplicates
import ValidateInput from '@/components/ValidateInput.vue'
import { defineComponent, onMounted, reactive, ref } from 'vue'
// eslint-disable-next-line import/no-duplicates
import { RulesProp } from '@/components/ValidateInput.vue'
import { useRoute, useRouter } from 'vue-router'
import axios from 'axios'
import Uploader from '@/components/Uploader.vue'
import { beforeUploadCheck } from '../helper'
import createMessage from '@/components/createMessage'
import EasyMDE, { Options } from 'easymde'
import Editor from '@/components/Editor.vue'
import { forStatement } from '@babel/types'
interface EditorInstance {
  clear:()=>void;
  getMDEInstance:()=>EasyMDE|null;
}
export default defineComponent({
  name: 'CreatePost',
  components: { ValidateForm, ValidateInput, Uploader, Editor },
  setup () {
    const upLoadedata = ref()
    const titleVal = ref('')
    const ContentVal = ref('')
    const route = useRoute()
    const router = useRouter()
    const isEditMode = !!route.query.id
    const textArea = ref<null | HTMLTextAreaElement>(null)
    const editOptions: Options = {
      // 拼写检查
      spellChecker: false
    }
    const editorStatus = reactive(
      {
        isPassed: true,
        message: ''
      }
    )
    const checkEditor = () => {
      if (ContentVal.value.trim() === '') {
        // eslint-disable-next-line no-unused-expressions
        editorStatus.isPassed = false
        editorStatus.message = '文章详情不能为空'
      } else {
        editorStatus.isPassed = true
        editorStatus.message = ''
      }
    }
    const editorRef = ref<null | EditorInstance>()
    onMounted(() => {
      if (isEditMode) {
        store.dispatch('fetchPostDeteil', route.query.id).then((rawData: ResponseType<PostProps>) => {
          const currentPost = rawData.data
          console.log(rawData.data.column)
          if (currentPost.image) {
            upLoadedata.value = { data: currentPost.image }
          }
          titleVal.value = currentPost.title
          ContentVal.value = currentPost.content || ''
        })
      }
    })
    let ImgId = ''
    const titleRules: RulesProp = [
      { type: 'required', message: '文章标题不能为空' }
    ]
    const ContentRules: RulesProp = [
      { type: 'required', message: '文章内容不能为空' }
    ]
    // 发布文章
    const onFormSubmit = (result: boolean) => {
      checkEditor()
      if (result && editorStatus.isPassed) {
        const { column, _id } = store.state.user
        if (column) {
          const newPost: PostProps = {
            title: titleVal.value,
            content: ContentVal.value,
            column,
            author: _id
          }
          if (ImgId) {
            newPost.image = ImgId
          }
          const actionName = isEditMode ? 'updatePost' : 'createPost'
          const senddata = isEditMode ? {
            id: route.query.id,
            payload: newPost
          } : newPost
          console.log(senddata)
          store.dispatch(actionName, senddata).then(() => {
            const sucMessage = createMessage('文章发布成功，正在跳转文章详情...', 'success')
            setTimeout(sucMessage.destory, 1000)
            setTimeout(() => {
              router.push({ name: 'column', params: { id: column } })
            }, 1000)
          }).catch(() => {
            const errMessage = createMessage('文章发布失败', 'error')
            setTimeout(errMessage.destory, 1000)
          })
        }
      }
    }
    // 上传图片
    const handleFileChange = (e: Event) => {
      const target = e.target as HTMLInputElement
      const files = target.files
      if (files) {
        const uploadedFile = files[0]
        const formdata = new FormData()
        formdata.append(uploadedFile.name, uploadedFile)
        axios.post('/upload', formdata, {
          headers: {
            'Content-Type': 'mulipart/form-data'
          }
        }).then((resp: any) => {
          console.log(resp)
        })
      }
    }
    // 图片校验
    const uploadCheck = (file: File) => {
      const result = beforeUploadCheck(file, { format: ['image/jpeg', 'image/png'], size: 0.5 })
      const { passed, error } = result
      if (error === 'format') {
        const errMessage = createMessage('图片只能为JPG/PNG格式', 'error')
        setTimeout(errMessage.destory, 1000)
      }
      if (error === 'size') {
        const errMessage = createMessage('图片大小不能大于0.5MB', 'error')
        setTimeout(errMessage.destory, 1000)
      }
      return passed
    }
    // 将图片id=文章id
    const handleFileUploaded = (rawData: ResponseType<ImageProps>) => {
      if (rawData.data._id) {
        ImgId = rawData.data._id
      }
    }
    return {
      titleVal,
      ContentVal,
      titleRules,
      ContentRules,
      onFormSubmit,
      handleFileChange,
      uploadCheck,
      handleFileUploaded,
      upLoadedata,
      isEditMode,
      textArea,
      editOptions,
      editorRef,
      checkEditor,
      editorStatus
    }
  }
})
