
import axios from 'axios'

import { computed, defineComponent, onMounted, ref } from 'vue'
import MarkdownIt from 'markdown-it'
import { useRoute, useRouter } from 'vue-router'
import store, { GlobalDataProps, PostProps, ImageProps, UserProps, ResponseType } from '@/store'
import { useStore } from 'vuex'
import PostList from '@/components/PostList.vue'
import UserProfile from '../components/UserProfile.vue'
import Model from '@/components/Model.vue'
import { forStatement } from '@babel/types'
import createMessage from '@/components/createMessage'
import router from '@/router'
import { marked } from 'marked'

export default defineComponent({
  name: 'PostDetail',
  components: { UserProfile, Model },
  setup () {
    const route = useRoute()
    const router = useRouter()
    const store = useStore<GlobalDataProps>()
    const postId = route.params.id
    const md = new MarkdownIt()
    const isModelVisiable = ref<boolean>(false)
    onMounted(() => {
      store.dispatch('fetchPostDeteil', postId)
    })
    const post = computed<PostProps>(() => store.getters.getCurrentPost(postId))
    const ImgUrl = computed(() => {
      if (post.value && post.value.image) {
        const { image } = post.value
        return (image as ImageProps).url + '?x-oss-process=image/resize,w_850'
      } else {
        return null
      }
    })
    // eslint-disable-next-line vue/return-in-computed-property
    const postHtml = computed(() => {
      const { content, isHTML } = post.value
      if (post.value && content) {
        return isHTML ? content : marked.parse(content)
      }
    })
    const showEditArea = computed(() => {
      const { isLogin, _id } = store.state.user
      if (post.value && post.value.author && isLogin) {
        const postAuthor = post.value.author as UserProps
        return postAuthor._id === _id
      } else {
        return false
      }
    })
    const hideAndDelete = () => {
      isModelVisiable.value = false
      store.dispatch('deletePost', postId).then((rawData: ResponseType<PostProps>) => {
        const message = createMessage('删除成功,正在跳转到专栏首页', 'success')
        setTimeout(() => {
          message.destory()
          router.push({ name: 'column', params: { id: rawData.data.column } })
        }, 2000)
      }).catch(() => {
        const message = createMessage('删除失败', 'error')
        setTimeout(() => {
          message.destory()
        }, 2000)
      })
    }
    return { postId, post, ImgUrl, postHtml, showEditArea, isModelVisiable, hideAndDelete }
  }

})
