
import { defineComponent, computed, onMounted, watch } from 'vue'
import 'bootstrap/dist/css/bootstrap.min.css'
import { useStore } from 'vuex'
import { GlobalDataProps, ResponseType, ImageProps } from './store'
import GlobalHeader from './components/GlobalHeader.vue'
import Loader from './components/Loader.vue'
import axios from 'axios'
import createMessage from './components/createMessage'
import unLogin from './components/unLogin.vue'
import Uploader from './components/Uploader.vue'
export default defineComponent({
  name: 'App',
  components: {
    // ColumnList,
    GlobalHeader,
    Loader,
    unLogin
  },
  setup () {
    const store = useStore<GlobalDataProps>()
    const currentUser = computed(() => store.state.user)
    const isLoading = computed(() => store.state.loading)
    const error = computed(() => store.state.error)
    watch(() => error.value.status, () => {
      const { status, message } = error.value
      if (status && message) {
        const errorMessage = createMessage(message, 'error')
        setTimeout(errorMessage.destory, 2000)
      }
    })
    const beforeupload = (file: File) => {
      const isJPG = file.type === 'image/jpeg'
      if (!isJPG) {
        const message = createMessage('请上传jpg格式文件', 'error')
        setTimeout(message.destory, 2000)
      }
      return isJPG
    }
    const onFileUploaded = (rawData: ResponseType<ImageProps>) => {
      console.log(rawData.data)
      const message = '上传成功'
      const sucMessage = createMessage(message, 'success')
      setTimeout(sucMessage.destory, 2000)
    }
    const onFileUploadederror = (errorData: ResponseType<ImageProps>) => {
      const eoorMessage = createMessage('上传失败！', 'error')
      setTimeout(eoorMessage.destory, 2000)
    }
    return {
      currentUser,
      isLoading,
      error,
      beforeupload,
      onFileUploaded,
      onFileUploadederror
    }
  }
})
