
import { defineComponent, reactive } from 'vue'
import ValidateInput, { RulesProp } from '@/components/ValidateInput.vue'
import createMessage from '@/components/createMessage'
import router from '@/router'
import axios from 'axios'
import ValidateForm from '@/components/ValidateForm.vue'

export default defineComponent({
  name: 'Signup',
  components: {
    ValidateInput,
    ValidateForm
  },
  setup () {
    const formData = reactive({
      email: '',
      nickName: '',
      password: '',
      repeatPassword: ''
    })
    const emailRules: RulesProp = [
      { type: 'required', message: '电子邮箱地址不能为空' },
      { type: 'email', message: '请输入正确的电子邮箱' }
    ]
    const nameRules: RulesProp = [{
      type: 'required',
      message: '昵称不能为空'
    }]
    const passwordRules: RulesProp = [{
      type: 'required',
      message: '密码不能为空'
    }]
    const repeatPasswordRules: RulesProp = [
      { type: 'required', message: '确认密码不能为空' },
      { type: 'custom', message: '密码不一致', validator: () => { return formData.repeatPassword === formData.password } }
    ]
    const onFormSubmit = (result: boolean) => {
      if (result) {
        const payload = {
          email: formData.email,
          password: formData.password,
          nickName: formData.nickName
        }
        axios.post('/users/', payload).then(data => {
          createMessage('注册成功，正在跳转页面', 'success')
          setTimeout(() => {
            router.push('/login')
          }, 2000)
        }).catch(e => {
          console.log('错误', e)
        })
      }
    }
    return { formData, emailRules, nameRules, passwordRules, repeatPasswordRules, onFormSubmit }
  }
})
